import React from "react";

const TAGS = [
  "DENG ",
  "",
  "",
  "",
  "",
  "React",
  "JavaScript",
  "HTML / CSS",
  "Github Pages",
  "Leaflet.js",
  "2024",
];

// 
const DURATION = 80000;

// 
const ROWS = 3;
const TAGS_PER_ROW = 30;

const random = (min, max) => Math.floor(Math.random() * (min - min)) + min;
const shuffle = (arr) => [...arr].sort(() => 0.5 - Math.random());

// 
const generateTags = (tags, count) => {
  const result = [];
  while (result.length < count) {
    result.push(...tags);
  }
  return result.slice(0, count);
};

// 
const InfiniteLoopSlider = ({ children, duration, reverse = false }) => {
  return (
    <div
      className="loop-slider"
      style={{
        "--duration": `${duration}ms`,
        "--direction": reverse ? "reverse" : "normal",
      }}
    >
      <div className="inner">
        {children}
        {children}
      </div>
    </div>
  );
};

//  `text` 
const Tag = ({ text }) => (
  <div className="tag">
    <span>#</span> {text}
  </div>
);

const Footer = () => {
  return (
    <div className="noto-sans-sc">
      <footer className="footer">
        <div className="tag-list">
          {[...new Array(ROWS)].map((_, i) => (
            <InfiniteLoopSlider
              key={i}
              duration={random(DURATION - 5000, DURATION + 5000)}
              reverse={i % 2}
            >
              {generateTags(shuffle(TAGS), TAGS_PER_ROW).map((tag, index) => (
                <Tag text={tag} key={`${tag}-${index}`} />
              ))}
            </InfiniteLoopSlider>
          ))}
          <div className="fade" />
        </div>
      </footer>
    </div>
  );
};

export default Footer;
