import React, { useState, useEffect } from "react";
import { MapContainer, TileLayer, Circle, Marker, Popup } from "react-leaflet";
import L from "leaflet";
import "leaflet/dist/leaflet.css";

const EarthquakeMap = ({ recentEarthquakes }) => {
  // 
  const [zoomLevel, setZoomLevel] = useState(() =>
    window.innerWidth < 768 ? 7 : 8.6
  );

  //  Popup 
  const [popupPosition, setPopupPosition] = useState(null);

  //  ()
  const [selectedEq, setSelectedEq] = useState(null);

  useEffect(() => {
    const updateZoomLevel = () => {
      setZoomLevel(window.innerWidth < 768 ? 7 : 8.6);
    };

    window.addEventListener("resize", updateZoomLevel);
    return () => window.removeEventListener("resize", updateZoomLevel);
  }, []);

  // 
  const customIcon = L.divIcon({
    className: "custom-marker",
    html: `<div style="width: 5px; height: 5px; background-color: black; border-radius: 50%;"></div>`,
    iconSize: [5, 5],
    iconAnchor: [2.5, 2.5],
  });

  //  
  //  
  //  M2.0 => 1 (1000m)
  //  M3.0 => 10 (10000m)
  //  M4.0 => 30 (30000m)
  //  M5.0 => 100 (100000m)
  //  M6.0 => 300 (300000m)
  //  M7.0 => 1000 (1000000m)
  //  M8.0 => 3000 (3000000m)

  const getRadiusFromMagnitude = (m) => {
    if (isNaN(m)) return 0;
    const pairs = [
      [2.0, 1000],
      [3.0, 10000],
      [4.0, 30000],
      [5.0, 100000],
      [6.0, 300000],
      [7.0, 1000000],
      [8.0, 3000000],
    ];

    if (m <= 2.0) return pairs[0][1];
    if (m >= 8.0) return pairs[pairs.length - 1][1];

    for (let i = 0; i < pairs.length - 1; i++) {
      const [x1, y1] = pairs[i];
      const [x2, y2] = pairs[i + 1];

      if (m >= x1 && m <= x2) {
        const ratio = (m - x1) / (x2 - x1);
        return y1 + ratio * (y2 - y1);
      }
    }
    return 0;
  };

  // 
  const defaultCenter = [23.6978, 120.9605];

  return (
    <div className="map-container">
      <MapContainer
        center={defaultCenter}
        zoom={zoomLevel}
        zoomSnap={0.2}
        zoomDelta={0.2}
        style={{ height: "100%", width: "100%" }}
      >
        <TileLayer
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          attribution="&copy; OpenStreetMap contributors"
        />

        {recentEarthquakes &&
          recentEarthquakes.map((eq, index) => {
            const lat = parseFloat(eq.latitude);
            const lng = parseFloat(eq.longitude);
            const magnitude = parseFloat(eq.magnitude);
            const originTime = eq.originTime || "";
            const earthquakeNo = eq.earthquakeNo || "";
            const webLink = eq.webLink || "";

            if (isNaN(lat) || isNaN(lng)) return null;

            const circleCenter = [lat, lng];
            const radius = getRadiusFromMagnitude(magnitude);

            //  eq.source  url1 =>  url2 => 
            const circleColor = eq.source === "url1" ? "green" : "red";

            return (
              <React.Fragment key={index}>
                <Circle
                  center={circleCenter}
                  radius={radius}
                  color={circleColor} // 
                  fillColor={circleColor} // 
                  fillOpacity={0.2} // 
                  weight={2} // 
                  opacity={0.1} // 
                  eventHandlers={{
                    //  Popup 
                    click: () => {
                      setPopupPosition(circleCenter);
                      setSelectedEq({
                        latitude: lat,
                        longitude: lng,
                        magnitude,
                        originTime,
                        earthquakeNo,
                        radius,
                        webLink,
                      });
                    },
                  }}
                />

                <Marker position={circleCenter} icon={customIcon} opacity={0.2}>
                  <Popup>
                    <div className="earthquake-info noto-sans-sc">
                      <p>
                        <b></b>
                      </p>
                      <p>{earthquakeNo}</p>
                      <p>{originTime}</p>
                      <p>{magnitude}</p>
                      <p>
                        {circleCenter[1]} / {circleCenter[0]}
                      </p>
                      <p> {Math.round(radius / 1000)} </p>
                      <a
                        href={webLink}
                        target="_blank"
                        rel="noopener noreferrer"
                      >
                        
                      </a>
                    </div>
                  </Popup>
                </Marker>
              </React.Fragment>
            );
          })}

        {popupPosition && selectedEq && (
          <Popup
            position={popupPosition}
            onClose={() => setPopupPosition(null)}
          >
            <div className="earthquake-info noto-sans-sc">
              <p>
                <b></b>
              </p>
              <p>{selectedEq.earthquakeNo}</p>
              <p>{selectedEq.originTime}</p>
              <p>{selectedEq.magnitude}</p>
              <p>
                {selectedEq.longitude} / {selectedEq.latitude}
              </p>
              <p>
                 {Math.round(selectedEq.radius / 1000)} 
              </p>
              <a
                href={selectedEq.webLink}
                target="_blank"
                rel="noopener noreferrer"
              >
                
              </a>
            </div>
          </Popup>
        )}
      </MapContainer>
    </div>
  );
};

export default EarthquakeMap;
